.App {
  height: 100vh auto;
  display:flex;
  flex-direction:row;
  color: white;
  justify-content: center; 
  align-items: center;
  margin-top: 5%;
}

#mainbox{
  width: 89%;
  height: 90% auto;
  color:rgb(231, 226, 225);
  font-family:Bookman, URW Bookman L, serif;
  background-color: rgb(11,11,11);
  align-self: center;
  display:flex;
  flex-direction:column;
  border-radius: 15px;
  box-shadow: 0px -25px 20px -25px rgba(110, 156, 194, 0.45),
              25px 0px 20px -25px rgba(110, 156, 194, 0.45),
              0px 25px 20px -25px rgba(110, 156, 194, 0.45),
              -25px 0px 20px -25px rgba(110, 156, 194, 0.45);
  font-size: 2.1vh;
  margin-bottom: 2%;
}

.nav-pill {
  margin-top: 2%;
  background-color: transparent;
  color: rgb(82, 78, 78);
  border: none;
  padding: 10px 20px;
  text-align: center;
  display: inline;
  font-size: 100%;
  cursor: pointer;
  transition: background-color 0.3s ease;
 }
 
 .nav-pill:hover {
  color: white;
 }
 
 .nav-pill.active {
  background-color: #1a73e8;
  color: white;
 }

 #contactme{
  margin-top: 7%;
  margin-left: 4%;
 }

 #about{
  margin: 4% 4% 2% 4%;
 }