body {
    font-family: Arial, sans-serif;
 }
 
 form {
   display:contents;
   flex-direction: column;
   width: 70%;
 }
 
 input[type="text"], input[type="email"], select {
    padding: 14px 20px;
    margin: 15px;
    border-radius: 2%;
    font-size: 2vh;
 }

 select{
   height: 45px;
 }

 textarea{
   padding: 14px 20px;
    margin: 15px;
    border-radius: 2%;
    font-size: 2vh;
    height:100px;
 }
 
 input[type="submit"] {
   display: grid;
    color: white;
 }
 
 input[type="submit"]:hover {
    opacity: 0.8;
 }
 
 #error {
    color: red;
    margin-left: 2%;
 }