.project-card {
    background-color: #1a73e8;
    padding: 20px;
    border-radius: 5px;
    transition: all 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94);
    margin: 2% 2% 2% 0%;
    cursor: pointer;
    flex-direction: column;
    display: inline-flex;
    width: auto;
    opacity: 90%;
   }
   
.project-card:hover {
    box-shadow: 0px -25px 20px -20px rgba(110, 156, 194, 0.45),
    25px 0px 20px -20px rgba(110, 156, 194, 0.45),
    0px 25px 20px -20px rgba(110, 156, 194, 0.45),
    -25px 0px 20px -20px rgba(110, 156, 194, 0.45);
}

.project-card h3 {
margin: 0;
color: #e6e7ed;
}

.project-card p {
margin: 10px 0 0 5;
color: #f0e9e9;
font-size: 2vh;
}

.project-card a {
    padding-top: 10px;
    color: #f0e9e9;
}